export default {
    props: {
        label: {
            type: String,
            default: '标签'
        },
        value: {
            type: String,
            default: ''
        },
        placeholder: {
            type: String,
            default: '请选择'
        },
        disabled: {
            type: Boolean,
            default: false
        },
        type: {
            type: String,
            default: 'text'
        },
        contentStyle: {
            type: String,
            default: ''
        },
        labelStyle: {
            type: String,
            default: ''
        },
        valueStyle: {
            type: String,
            default: ''
        },
        other: {
            type: [Object, Number, String, Array, Boolean],
            default: ''
        }
    },
    methods: {
        inputVal(val) {
            this.$emit('input', {
                label: this.label,
                value: val.detail.value,
                disabled: this.disabled,
                other:this.other
            })
        },
        selectCell() {
            this.$emit('click', {
                label: this.label,
                value: this.value,
                disabled: this.disabled,
                other:this.other
            })
        }
    },
}
